	}

	.note-control-sizing {
		width: 7px;
		height: 7px;
		border: 1px solid $black;
		background-color: $black;
	}

	.note-control-nw {
		top: -5px;
		left: -5px;
		border-right: none;
		border-bottom: none;
	}

	.note-control-ne {
		top: -5px;
		right: -5px;
		border-bottom: none;
		border-left: none;
	}

	.note-control-sw {
		bottom: -5px;
		left: -5px;
		border-top: none;
		border-right: none;
	}

	.note-control-se {
		right: -5px;
		bottom: -5px;
		cursor: se-resize;

		&.note-control-holder {
			cursor: default;
			border-top: none;
			border-left: none;
		}
	}

	.note-control-selection-info {
		right: 0;
		bottom: 0;
		padding: 5px;
		margin: 5px;
		color: $white;
		background-color: $black;
		font-size: 12px;
		border-radius: 5px;
		-webkit-opacity: 0.7;
		-khtml-opacity: 0.7;
		-moz-opacity: 0.7;
		opacity: 0.7;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=70);
		filter: alpha(opacity = 70);
	}
}

.note-hint-popover {
	min-width: 100px;
	padding: 2px;

	.popover-content {
		padding: 3px;
		max-height: 150px;
		overflow: auto;

		.note-hint-group .note-hint-item {
			display: block !important;
			padding: 3px;

			&.active,
			&:hover {
				display: block;
				clear: both;
				font-weight: 400;
				line-height: 1.4;
				color: $white;
				white-space: nowrap;
				text-decoration: none;
				background-color: $primary;
				outline: 0;
				cursor: pointer;
			}
		}
	}
}

.note-btn {
	&.dropdown-toggle::after {
		display: none;
	}

	border-radius: 0px;
}

.note-editor.note-frame.panel.panel-default .panel-heading {
	background-color: $white;
	border-bottom: 1px solid $border;
}

.note-btn.btn-default {
	background-color: $white;
	border:1px solid $border;
}

.note-editor {
	.btn-default:not(:disabled):not(.disabled):active {
		background-color: $background;
	}

	.btn-group .btn + .btn-group {
		border-left: 0px solid rgba(0, 0, 0, 0.08) !important;
	}
}

.panel-default.fullscreen .note-editable {
	background-color: $white;
}

.note-btn {
	&.btn.btn-default.btn-sm {
		line-height: 1.84615385;
	}

	&.btn-default:not(:disabled):not(.disabled).active {
		color: $color;
		background-color: $background;
		border-color: $border;
	}
}
